* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

body {
  background: linear-gradient(
    90deg,
    rgb(153, 153, 153) 0%,
    rgb(90, 90, 90) 100%
  );
}
.todo-app {
  display: flex;
  flex-direction: column;
  justify-content: start;
  width: 520px;
  min-height: 600px;
  background: #272b3f;
  text-align: center;
  margin: 128px auto;
  border-radius: 10px;
  padding-bottom: 32px;
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.8);
}

h1 {
  margin: 32px 0;
  color: #fff;
  font-size: 24px;
}

.complete {
  text-decoration: line-through;
  opacity: 0.4;
}

.todo-input {
  padding: 14px 32px 14px 16px;
  border-radius: 4px 0 0 4px;
  border: 2px solid rgb(97, 86, 192);
  outline: none;
  width: 320px;
  background: transparent;
  color: #fff;
  margin-bottom: 32px;
}

.todo-input::placeholder {
  color: #e2e2e2;
}

.todo-button {
  padding: 16px;
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  outline: none;
  background: linear-gradient(
    90deg,
    rgb(97, 86, 192) 0%,
    rgb(66, 59, 124) 100%
  );
  color: #fff;
  text-transform: capitalize;
}
.inactive {
  display: none;
}

.todo-input.edit {
  border: 2px solid #944e4c;
  margin-bottom: 0px;
}

.todo-button.edit {
  background: linear-gradient(
    90deg,
    rgb(172, 76, 76) 0%,
    rgb(139, 38, 38) 100%
  );
  padding: 16px 52px;
}

.todo-container {
  display: flex;
  flex-direction: row;
  position: relative;
}

.todo-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 4px auto;
  color: #fff;
  background: linear-gradient(
    90deg,
    rgb(216, 162, 91) 0%,
    rgb(165, 124, 71) 100%
  );

  padding: 16px;
  border-radius: 5px;
  width: 90%;
}

.todo-row:nth-child(4n + 1) {
  background: linear-gradient(
    90deg,
    rgb(139, 109, 197) 0%,
    rgb(120, 77, 146) 100%
  );
}

.todo-row:nth-child(4n + 2) {
  background: linear-gradient(
    90deg,
    rgb(131, 177, 187) 0%,
    rgb(92, 128, 136) 100%
  );
}

.todo-row:nth-child(4n + 3) {
  background: linear-gradient(
    90deg,
    rgb(96, 163, 211) 0%,
    rgb(50, 108, 184) 100%
  );
}

.icons {
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
}

.delete-icon {
  margin-right: 5px;
  color: #fff;
}

.edit-icon {
  color: #fff;
}
